import React  from 'react'
import { Formik, Form } from "formik";
import * as Yup from "yup";
import Button from "@material-ui/core/Button";

import FormikField from './FormikField';
import './Login.css';

const initialValues = {
    userName: "",
    password: ""
  };

const SignupSchema = Yup.object().shape({
    userName: Yup.string()
      .min(2, "太短!")
      .required("必须"),
      password: Yup.string().required("必须")
  });

function Login() {

    const handleSubmit = (values) => {
        alert(JSON.stringify(values) + test);
      };

    return (
        <div className="mystyle mystyle2">
            <h1>登陆</h1>    
            <Formik
            initialValues={initialValues}
            onSubmit={handleSubmit}
            validationSchema={SignupSchema}
            >
                {({ dirty, isValid })=>{
                    return(
                        <Form>
                            <FormikField name="userName" label="用户名" required />
                            <FormikField name="password" label="密码" type="password" required />
<div className="btn">
                            <Button
                variant="contained"
                color="primary"
                disabled={!dirty || !isValid}
                type="submit"
              >
                Submit
              </Button>
              </div>



                        </Form>



                    );

                }}

            </Formik>
            



        </div>
    )
}

export default Login
