import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from 'react-bootstrap/Card';

const useStyles = makeStyles({
    root: {
  
    },
    media: {
  
      with: '100%',
      height: 'auto',
      // paddingTop: '74.66%', // 16:9,
      // marginTop:'100'
    },
    priceft: {
      display: 'flex',
      justifyContent: 'flex-end',
    },
    price1: {
      textDecoration: 'line-through',
      marginRight: '30px',
  
    },
    price2: {
      color: 'red',
      marginRight: '40px',
    },
  });

function Product2({imgLink, desc}) {
    const classes = useStyles();
    return (
        <Card>
        <Card.Img variant="top" src={imgLink} className={classes.media}/>
        <Card.Body>
          <Card.Text>
           {desc}
          </Card.Text>
        </Card.Body>
      </Card>
    )
}

export default Product2
